.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.headerImageDiv {
  width: 100vw;
  height: 100vh;
  align-content: center;

  .dsds {
    width: 200px;
    height: 200px;
  }

  //.headerimage {
  //  filter: brightness(50%);
  //  width: 100vw;
  //  height: 100vh;
  //
  //}

  .headerimage {
    opacity: 1;
    transition: opacity 0.3s;
    filter: brightness(50%);
    width: 100vw;
    height: 100vh;
  }

  .headerimage[data-src] {
    opacity: 0;
  }


  .navDiv{
    position: absolute;
    height: 7vh;
    align-items: center;
    width: 100vw;
    text-align: center;
    justify-content: center;
    display: flex;
    flex-direction: row;

  }

  .headerImageTypingDiv {
    position: absolute;
    top: 20vh;
    width: 100vw;
    text-align: center;

    .headerImageTyping {
      color: #FFFFFF;
      font-weight: bold;
      font-size: 24px;
      height: 7vh;
    }

  }

  .headerFlashDiv {

    cursor: pointer;
    position: absolute;
    bottom: 10px;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;

    .headerFlash {
      font-size: 50px;
      color: #FFFFFF;
    }

  }
}

.projectsSampleDiv {
  width: 100vw;

  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
  align-content: space-around;

}

.skillDiv {
  padding-top: 20px;
  //@extend .projectsSampleDiv;
  width: 100vw;
  height: 100vh;

  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
  align-content: space-around;

  //.skillimage {
  //  filter: brightness(50%);
  //  width: 100vw;
  //  height: 100vh;
  //
  //}

  .skillimage {
    opacity: 1;
    transition: opacity 0.3s;
    filter: brightness(50%);
    width: 100vw;
    height: 100vh;
  }

  .skillimage[data-src] {
    opacity: 0;
  }

}

html {
  scroll-behavior: smooth;
}

.contactDiv {
  width: 100vw;
  height: 40vh;
  align-items: center;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;

  .contactBTN{
    width:3vw;
    height:3vw;
    margin-right: 10px;
    margin-left: 10px;
    margin-top: 10px;
    cursor: pointer;

  }



}




@media screen and (max-width: 1200px) {


  .headerImageDiv {
    width: 100vw;
    height: 35vh;
    align-content: center;

    .dsds {
      width: 200px;
      height: 200px;
    }

    .headerimage {
      filter: brightness(50%);
      width: 100vw;
      height: 35vh;

    }

    .navDiv{
      position: absolute;
      height: 7vh;
      align-items: center;
      width: 100vw;
      text-align: center;
      display: flex;
      flex-direction: row;


      justify-content: center;

    }

    .headerImageTypingDiv {
      position: absolute;
      top: 10vh;
      width: 100vw;
      text-align: center;

      .headerImageTyping {
        color: #FFFFFF;
        font-weight: bold;
        font-size: 5vw;
        height: 7vh;
      }

    }

    .headerFlashDiv {

      display: none;

      .headerFlash {
        font-size: 50px;
        color: #FFFFFF;
      }

    }
  }

  .contactDiv {
    width: 100vw;
    height: 35vh;
    align-items: center;
    display: flex;
    flex-direction: column;
    background-color: #ffffff;

    .contactBTN{
      width:10vw;
      height:10vw;
      margin-right: 10px;
      margin-left: 10px;

      cursor: pointer;

    }



  }

  .skillDiv {
    padding-top: 20px;
    //@extend .projectsSampleDiv;
    width: 100vw;
    height: 40vh;

    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
    align-content: space-around;

    .skillimage {
      filter: brightness(50%);
      width: 100vw;
      height: 40vh;

    }

  }

}


